55,255,255,0) 0%,#356BA1 100%,rgba(255,255,255,1) 100%);\n        position: absolute;\n        top: 0; left: 0;\n      }\n    }\n    top-header {\n      position: relative;\n      z-index: 1;\n    }\n    navigation {\n      position: relative;\n      z-index: 1;\n    }\n  } \n}",".main-wrapper {\n  height: 100%;\n}\n.main-wrapper .content-wrapper {\n  width: 80%;\n  margin: 0 auto;\n  max-width: 1200px;\n  min-width: 400px;\n  height: 100%;\n}\n.main-wrapper .content-wrapper .overlay {\n  position: absolute;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  left: 0;\n  z-index: 0;\n}\n.main-wrapper .content-wrapper .overlay:after {\n  content: \"\";\n  display: block;\n  width: 100%;\n  height: 100%;\n  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 0%, #356BA1 100%, #ffffff 100%);\n  position: absolute;\n  top: 0;\n  left: 0;\n}\n.main-wrapper .content-wrapper top-header {\n  position: relative;\n  z-index: 1;\n}\n.main-wrapper .content-wrapper navigation {\n  position: relative;\n  z-index: 1;\n}\nnavigation {\n  width: 100%;\n  height: 100px;\n  display: inline-block;\n}\nnavigation .nav-wrapper {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n}\nnavigation .nav-wrapper > div {\n  flex: 1;\n  text-align: center;\n  height: 100%;\n  width: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  border-radius: 4px;\n  margin: 2px;\n  position: relative;\n  transition: all 0.2s ease-in-out;\n}\nnavigation .nav-wrapper > div:hover {\n  cursor: pointer;\n  background-color: rgba(0, 0, 0, 0.8);\n  transition: all 0.2s ease-in-out;\n}\nnavigation .nav-wrapper > div .title-icon {\n  font-size: 3.0em;\n  margin-top: 17px;\n  color: #ccc;\n}\nnavigation .nav-wrapper > div .title {\n  position: absolute;\n  bottom: 12px;\n  left: 0;\n  right: 0;\n  color: #e1e1e1;\n  font-size: 1.4em;\n  font-weight: bold;\n}\ntop-header {\n  width: 100%;\n  height: 135px;\n  display: inline-block;\n  box-sizing: border-box;\n  padding: 5px;\n}\ntop-header .header-wrapper {\n  height: 100%;\n  width: 100%;\n  padding: 5px;\n  box-sizing: border-box;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 4px;\n  border: 1px solid rgba(0, 0, 0, 0.2);\n}\ntop-header .header-wrapper > div {\n  height: 100%;\n}\ntop-header .header-wrapper .team-info {\n  flex: 1;\n}\ntop-header .header-wrapper .team-info .team-logo {\n  width: 110px;\n  height: 110px;\n  display: inline-block;\n  margin-left: 5px;\n}\ntop-header .header-wrapper .team-info .team-longname {\n  display: inline-block;\n  vertical-align: top;\n  width: calc(100% - 130px);\n  padding-left: 10px;\n  box-sizing: border-box;\n  padding-top: 15px;\n}\ntop-header .header-wrapper .team-info .team-longname .team-name {\n  font-size: 1.5em;\n  font-weight: 400;\n  color: #fff;\n  font-style: italic;\n}\ntop-header .header-wrapper .team-info .team-longname .team-locale {\n  font-size: 1.2em;\n  font-weight: 400;\n  color: #999;\n}\ntop-header .header-wrapper .shockball-logo-holder {\n  flex: 2;\n  text-align: center;\n  position: relative;\n}\ntop-header .header-wrapper .shockball-logo-holder .shockball-logo {\n  width: 140px;\n  height: 140px;\n  position: absolute;\n  left: 0;\n  right: 0;\n  margin: auto;\n  top: 10px;\n}\ntop-header .header-wrapper .shockball-logo-holder .shockball-title {\n  position: absolute;\n  left: 0;\n  right: 0;\n  margin: auto;\n  top: 33px;\n  color: #fff;\n  font-size: 3em;\n  font-weight: 900;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n}\ntop-header .header-wrapper .upcoming-match-holder {\n  flex: 1;\n  box-sizing: border-box;\n  border-radius: 4px;\n  border: 1px solid transparent;\n  max-width: 320px;\n}\ntop-header .header-wrapper .upcoming-match-holder .header {\n  background-color: #0C93EB;\n  border-top-left-radius: 4px;\n  border-top-right-radius: 4px;\n  padding: 3px;\n  color: #fff;\n}\ntop-header .header-wrapper .upcoming-match-holder .upcoming-content {\n  height: 88px;\n  background-color: rgba(0, 0, 0, 0.2);\n  border-bottom-left-radius: 4px;\n  border-bottom-right-radius: 4px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\ntop-header .header-wrapper .upcoming-match-holder .upcoming-content .upcoming-team {\n  flex: 1;\n}\ntop-header .header-wrapper .upcoming-match-holder .upcoming-content .upcoming-team .upcoming-team-logo {\n  width: 50px;\n  height: 50px;\n  margin: 0 auto;\n  margin-left: 40px;\n}\ntop-header .header-wrapper .upcoming-match-holder .upcoming-content .upcoming-team .upcoming-team-name {\n  color: #fff;\n  box-sizing: border-box;\n  padding-top: 4px;\n  padding-left: 7px;\n}\ntop-header .header-wrapper .upcoming-match-holder .upcoming-content .countdown {\n  flex: 1;\n  color: #999;\n}\nhtml,\nbody,\nsim {\n  height: 100%;\n  font-family: Rubik, Arial, serif;\n}\n.wrapper {\n  height: 100%;\n}\nmain,\nsim,\nsim > div {\n  height: 100%;\n}\nbody {\n  color: black;\n  margin: 0;\n}\nh1 {\n  display: inline-block;\n  vertical-align: top;\n  padding-top: 0;\n  margin-top: 0;\n  padding-left: 10px;\n}\n.header-title {\n  color: #fff;\n  margin: 8px;\n  display: inline-block;\n  width: auto;\n  border-radius: 4px;\n  background-color: rgba(0, 0, 0, 0.7);\n  padding: 6px;\n}\n.header-title:hover {\n  cursor: pointer;\n}\n.gameObject {\n  color: Red;\n  background-color: #ddd;\n  border-radius: 5px;\n  padding: 12px;\n  display: inline-block;\n  max-width: 475px;\n}\nsim {\n  background-color: #F5F5F5;\n}\nsim > div {\n  background-image: url(\"img/shockball2.jpg\");\n  background-size: cover;\n  background-position: center center;\n  background-repeat: no-repeat;\n  background-attachment: fixed;\n}\n.button {\n  width: auto;\n  display: inline-block;\n  box-sizing: border-box;\n  padding: 5px;\n  background-color: #397297;\n  color: #fff;\n  transition: all 0.2s ease-in-out;\n  border-radius: 4px;\n}\n.button:hover {\n  cursor: pointer;\n  backgroun